	module.exports = {"data":{"markdownRemark":{"html":"<p>I'm killing a cold for several days and dying. <a href=\"/!/thA/\">It seems</a> that <a href=\"/!/thA/\">Eastern time Sealing Castle</a> Production will also be stalled and trial version B will be delayed by several days. I'm sorry. And a little report was delayed, but we won the summer comic. <em>On Sunday, August 14 (Sun), the place is west-06a</em> . Exhibits will be <a href=\"/!/thA/\">sealed</a> to the <a href=\"/!/thA/\">east</a> . Since we will continue to produce continuously, today we will be updating only the announcement. I'm still tough, but if I only delay the trial version, I will do it anyway and it will be the worst ('A`)</p>","frontmatter":{"cover":null,"date":"2005-05-24T00:00:00+09:00","strDate":"2005/5/23","title":"Winner of the summer comic"}}},"pathContext":{"path":"/en/blog/2005/05/24-夏コミ当選/","langKey":"en"}}

